.results-list {
  background-color: #f6f6f6;
  padding: 2rem;
  padding-left: 4rem;
  max-width: 600px;
  border-radius: 10px;
  margin: auto;
  margin-top: 2rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  box-shadow: 5px 5px 5px #888888;

  li {
    text-transform: capitalize;
    padding-bottom: 1.2rem;
  }
}

li:nth-child(even) {
  display: none;
}

::marker {
  visibility: hidden;
}
